var first = (function() {
    var init = function() {
        $(function() {
            var plsslider = $('.plsslider').owlCarousel({
                loop: true,
                margin: 5,
                items: 3,
                dots: false,
                smartSpeed: 1500,
                responsive: {
                    0: {
                        items: 1
                    },
                    768: {
                        items: 3
                    },
                    992: {
                        items: 2
                    },
                    1200: {
                        items: 3
                    }
                }
            });

            $('.controls__btn_next').on('click', function(e) {
                e.preventDefault();

                plsslider.trigger('next.owl.carousel');
            });

            $('.controls__btn_prev').on('click', function(e) {
                e.preventDefault();

                plsslider.trigger('prev.owl.carousel');
            });


            //second slider 
            var secslider = $('.secslider').owlCarousel({
                loop: true,
                margin: 40,
                items: 4,
                dots: false,
                smartSpeed: 1500,
                responsive: {
                    0: {
                        items: 1
                    },
                    768: {
                        items: 3
                    },
                    1200: {
                        items: 4
                    }
                }

            });

            $('.secslider__btn_next').on('click', function(e) {
                e.preventDefault();

                secslider.trigger('next.owl.carousel');
            });

            $('.secslider__btn_prev').on('click', function(e) {
                e.preventDefault();

                secslider.trigger('prev.owl.carousel');
            });


            // header slider 
            var restore = $('.restore').owlCarousel({
                loop: true,
                margin: 10,
                items: 2,
                dots: false,
                smartSpeed: 1500,
                responsive: {
                    0: {
                        items: 1
                    },
                    768: {
                        items: 2
                    },
                    992: {
                        items: 4
                    },
                    1200: {
                        items: 8
                    }
                }
            });
            $('.restore__btn_next').on('click', function(e) {
                e.preventDefault();

                restore.trigger('next.owl.carousel');
            });

            $('.restore__btn_prev').on('click', function(e) {
                e.preventDefault();

                restore.trigger('prev.owl.carousel');
            });

            // $(".add li").slice( 2 ).css({"position": "relative","top": "-2px"});

        });
    }

    return {
        init: init
    };
})();
