var third = (function() {
    var init = function() {
        $(function() {

            // myCarousel itempage
            $('#myCarousel').carousel({
                interval: 5000
            });

            //Handles the carousel thumbnails
            $('[id^=carousel-selector-]').hover(function() {
                var id_selector = $(this).attr("id");
                try {
                    var id = /-(\d+)$/.exec(id_selector)[1];
                    // console.log(id_selector, id);
                    jQuery('#myCarousel').carousel(parseInt(id));
                } catch (e) {
                    console.log('Regex failed!', e);
                }
            });
            // When the carousel slides, auto update the text
            $('#myCarousel').on('slid.bs.carousel', function(e) {
                var id = $('.item.active').data('slide-number');
                $('#carousel-text').html($('#slide-content-' + id).html());
            });

            // myCarousel itempage End


            // hover zoom effect

            $('.restoran__link').hover(function() {
                /* Stuff to do when the mouse enters the element */
                $('.restoran__simg').addClass('scale');
                $('.restoran__link .restoran__img').addClass('scale');
            }, function() {
                /* Stuff to do when the mouse leaves the element */
                $('.restoran__simg').removeClass('scale');
                $('.restoran__link .restoran__img').removeClass('scale');
            });

            // hover zoom delivery 
            if (($(window).width() > 767)) {
                $('.plus-img__wrap').hover(function() {
                    /* Stuff to do when the mouse enters the element */
                    $(this).css('transform', 'scale(1.3)');
                }, function() {
                    /* Stuff to do when the mouse leaves the element */
                    $(this).css('transform', 'scale(1)');
                });
            }

            // hover zoom single effect
            $('.restoran__simg').hover(function() {
                /* Stuff to do when the mouse enters the element */
                $(this).addClass('single-scale');

            }, function() {
                /* Stuff to do when the mouse leaves the element */
                $(this).removeClass('single-scale');
            });

            // hover zoom effect 2 image
            if (($(window).width() > 767)) {
                $('.pom__link').hover(function() {
                    /* Stuff to do when the mouse enters the element */
                    $('.pom__link .restoran__img').css({
                        transform: 'scale(1.1)'
                    });
                    $('.pom__img').css({
                        transform: 'scale(1.2)'
                    });
                }, function() {
                    /* Stuff to do when the mouse leaves the element */
                    $('.pom__link .restoran__img').removeAttr('style');
                    $('.pom__img').removeAttr('style');
                });
            }

            var wow = new WOW({
                mobile: false
            });

            wow.init();
            AOS.init();

            // fixed header
            var ht = $('.header__top').height();
            var hb = $('.header__bottom').height();
            $('.header__bottom').css('marginTop', ht);
            $('.bredwrap_mlyn').css('marginTop', ht);
            // add different margin-top to header-bottom depence of header-top height
            $(window).resize(function() {
                var ht = $('.header__top').height();
                $('.header__bottom').css('marginTop', ht);
                $('.bredwrap_mlyn').css('marginTop', ht);
            });
            // add animation , when user scroll window
            $(window).scroll(function() {
                /* Act on the event */
                if (($(this).scrollTop() > ht + hb) && ($(window).width() > 767)) {
                    $('.header__top').addClass('animated fadeInDown');
                } else {
                    $('.header__top').removeClass('animated fadeInDown');
                }
            });

            // Tabs checkout
            $('#myTabs a').click(function(e) {
                e.preventDefault();
                $(this).tab('show');
            });

            $('.brownlist_menu .brownlist__link').focusin(function() {
                $(this).parent().css({
                    display: 'block',
                    width: '14%',
                    marginTop: '10px',
                    marginBottom: '10px',
                    marginLeft: 'auto',
                    marginRight: 'auto'
                });
                $(this).parent().addClass('changebefore').attr('data-content', '');

            });

            $('.brownlist_menu .brownlist__link').focusout(function() {
                $(this).parent().removeAttr('style');
                $(this).parent().attr('data-content', '/');
            });

            // delivery scroll script
            var target = $('#menu1').offset().top;
            var target2 = $('#menu2').offset().top - 370,
                target3 = $('#menu3').offset().top - 370,
                timeout = null;



            $(window).scroll(function() {
                if ($(window).width() > 767) {
                    if (!timeout) {
                        timeout = setTimeout(function() {
                            console.log('scroll');
                            clearTimeout(timeout);
                            timeout = null;
                            if ($(window).scrollTop() >= target - ht && $(window).scrollTop() < target2) {
                                $('#menu1').css({
                                    position: 'fixed',
                                    top: '90px',
                                    left: '0',
                                    zIndex: '5',
                                    width: '100%',
                                    backgroundColor: '#fff'
                                });
                                $('#menu1').addClass('animated fadeInDown');
                                $('#menu2').removeClass('animated fadeInDown');
                                $('#menu3').removeClass('animated fadeInDown');
                                $('#menu2').removeAttr('style');
                                $('#menu3').removeAttr('style');
                            } else if ($(window).scrollTop() >= target2 && $(window).scrollTop() < target3) {
                                $('#menu2').css({
                                    position: 'fixed',
                                    top: '90px',
                                    left: '0',
                                    zIndex: '5',
                                    width: '100%',
                                    backgroundColor: '#fff'
                                });
                                $('#menu2').addClass('animated fadeInDown');
                                $('#menu1').removeAttr('style');
                                $('#menu1').removeClass('animated fadeInDown');
                                $('#menu3').removeAttr('style');
                                $('#menu3').removeClass('animated fadeInDown');
                            } else if ($(window).scrollTop() >= target3) {
                                $('#menu3').css({
                                    position: 'fixed',
                                    top: '90px',
                                    left: '0',
                                    zIndex: '5',
                                    width: '100%',
                                    backgroundColor: '#fff',
                                    paddingBottom: '10px'
                                });
                                $('#menu3').addClass('animated fadeInDown');
                                $('#menu1').removeClass('animated fadeInDown');
                                $('#menu2').removeClass('animated fadeInDown');
                                $('#menu1').removeAttr('style');
                                $('#menu2').removeAttr('style');
                            } else {
                                $('#menu1').removeAttr('style');
                                $('#menu2').removeAttr('style');
                                $('#menu3').removeAttr('style');
                            }
                        }, 250);
                    }
                }
            });




        });
    }
    return { init: init };
})();
